import React from "react";

type ProfileStatusPropsType = {}

class ProfileStatus extends React.Component<ProfileStatusPropsType> {
    state = {
        editMode: false,
    }

    activateEditMode = () => {
        this.setState({editMode: true})
        /*alert('setTrue')*/
    }
    deactivateEditMode = () => {
        this.setState({editMode: false})
        /*alert('setFalse')*/
    }

    render() {

        return (
            <>
                {
                    !this.state.editMode &&
                        <div>
                            <div onDoubleClick={this.activateEditMode}>{"hello"}</div>
                        </div>
                }

                {
                    this.state.editMode &&
                        <div>
                            <input autoFocus onBlur={this.deactivateEditMode} value={"hello"}></input>
                        </div>
                }
            </>
        )
    }
}

export  {
    ProfileStatus
}